	module.exports = {"data":{"markdownRemark":{"html":"<h2>HTML5 Optional Tags</h2>\n<p>In HTML5, you can omit certain opening and closing tags under specific conditions. For example, the following HTML code…</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token doctype\">&lt;!DOCTYPE html></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>Hello World.\n</code></pre>\n      </div>\n<p>Will automatically evaluate to…</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token doctype\">&lt;!DOCTYPE html></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>html</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>head</span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>head</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>body</span><span class=\"token punctuation\">></span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>Hello world.\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>body</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>html</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>The optional tag specifications for the most common HTML5 tags are as follows:</p>\n<ul>\n<li>An <code>html</code> element’s start tag may be omitted if the first thing inside the <code>html</code> element is not a comment.</li>\n<li>An <code>html</code> element’s end tag may be omitted if the <code>html</code> element is not immediately followed by a comment.</li>\n<li>A <code>head</code> element’s start tag may be omitted if the element is empty, or if the first thing inside the <code>head</code> element is an element.</li>\n<li>A <code>head</code> element’s end tag may be omitted if the <code>head</code> element is not immediately followed by a space character or a comment.</li>\n<li>A <code>body</code> element’s start tag may be omitted if the element is empty, or if the first thing inside the <code>body</code> element is not a space character or a comment, except if the first thing inside the <code>body</code> element is a <code>meta</code>, <code>link</code>, <code>script</code>, <code>style</code>, or <code>template</code> element.</li>\n<li>A <code>body</code> element’s end tag may be omitted if the body element is not immediately followed by a comment.</li>\n</ul>\n<h3>More Information</h3>\n<p>To learn more about the HTML5 optional tags, please visit <img src=\"https://www.w3.org/TR/html5/syntax.html#optional-tags\" alt=\"The World Wide Web Consortium&#x27;s Recommendations\">.</p>","fields":{"slug":"/html/optional-tags/"},"frontmatter":{"title":"Optional Tags"}}},"pathContext":{"slug":"/html/optional-tags/"}}

